<template>
  <div class="editcategory-container">
    <div class="header">
      商品类目：<input type="text" v-model="editInput" class="edittitleinput" />
      id:<input type="text" v-model="editid" class="edittitleinput" />
    </div>
    <div class="bodyer">
      商品子类目：
      <el-tag
        :key="tag"
        v-for="tag in dynamicTags"
        closable
        :disable-transitions="false"
        @close="handleClose(tag)"
      >
        {{ tag }}
      </el-tag>
      <el-input
        class="input-new-tag"
        v-if="inputVisible"
        v-model="inputValue"
        ref="saveTagInput"
        size="small"
        @keyup.enter.native="handleInputConfirm"
        @blur="handleInputConfirm"
      >
      </el-input>
      <el-button v-else class="button-new-tag" size="small" @click="showInput"
        >+ New Tag</el-button
      >
    </div>
    <el-row>
      <el-button type="success" @click="editsuccess">提交</el-button>
      <el-button type="danger" @click="editdanger">取消</el-button>
    </el-row>
  </div>
</template>

<script>
import api from '@/api/product.js';
export default {
  data() {
    return {
      dynamicTags: [],
      inputVisible: false,
      inputValue: "",
      editInput: "",
      editid: "",
    };
  },
  methods: {
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    editsuccess() {
        api.editCategory({id: this.editid, name: this.editInput, c_items: this.dynamicTags}).then((res)=>{
            if(res.status == 'success') {
                this.$router.push({name: 'category'})
            }
        })
    },
    editdanger() {
        this.$router.push({name: 'category'})
    }
  },
  created(){
    this.dynamicTags = this.$store.state.editnumber.c_items;
    this.editInput = this.$store.state.editnumber.name;
    this.editid = this.$store.state.editnumber.id
  }
};
</script>

<style scoped lang="less">
.editcategory-container {
  width: 40%;
  margin: 100px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  div {
    margin: 10px 0;
  }
  .bodyer {
    // display: flex;
    span,
    div,
    button {
      margin: 10px;
    }
  }
}
.edittitleinput {
  width: 100px;
  margin: 0 10px;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>